.form {
    width: 80%;
    margin: auto;
}

.NameEmail {
    width: 100%;
}

.Name {
    width: 40%;
    float: left;
}

.Email {
    width: 55%;
    float: right;
}

.PhoneMajor {
    width: 100%;
}

.Phone {
    width: 40%;
    float: left;
}

.Major {
    width: 55%;
    float: right;
}

.CommitteePosition {
    width: 100%;
}

.Committee {
    width: 40%;
    float: left;
}

.Position {
    width: 55%;
    float: right;
}

.UploadSubmit {
    clear: both;
}

.UploadImage {
    width: 50%;
    float: left;
}

.Submit {
    width: 40%;
    float: right;
    padding: 2rem;
    padding-right: 0;
}

@media (max-width: 767px) {
    .form {
        width: 100%; /* Full width for mobile screens */
    }

    /* Adjustments to stack elements in their own row for mobile */
    .Name,
    .Email,
    .Phone,
    .Major,
    .Committee,
    .Position,
    .UploadImage,
    .Submit {
        width: 100%;
        float: none;
        clear: both;
        padding: 0;
    }
}