/* CSS for the form layout */
.form-container {
    display: flex;
    flex-direction: column;
    width: 60%;
    margin: 0 auto;
  }
  
  .form-row {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .form-label {
    width: 30%;
    text-align: right;
    padding-right: 15px;
  }
  
  .form-input {
    width: 70%;
  }
  
  /* Additional styles for button alignment */
  .submit-button {
    margin-top: 20px;
    text-align: center;
  }
  